import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getTestPackages } from 'services/testPackage';
import { getListSubject } from 'services/Subject';
// import { getTestRoomForSelect } from 'services/testOrganizing';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn học phần',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Hệ">
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={6}>
            <Form.Item label="Học phần">
              {getFieldDecorator('subjectId', optionFieldDecorator.subjectId)(
                <SelectAjax service={getListSubject} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('system4', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn lần thi!',
                  },
                ]
              })(
                <Select>
                  <Option value=" 1">Thi lần 1</Option>
                  <Option value=" 2">Thi lần 2</Option>
                  <Option value=" 3">Thi lần 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('examBatch ', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn đợt thi!',
                  },
                ]
              })(
                <InputNumber />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Túi thi số">
              {getFieldDecorator('system3', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn túi thi!',
                  },
                ]
              })(<SelectAjax service={getTestPackages} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phòng thi">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                  <Option value="3">3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



