import React, { Component } from 'react';
import { Row, Col, Form, Select, Input, Button } from 'antd';
import { BtnAddNew, BtnDelete } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar1 extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Túi thi số">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">1</Option>
                  <Option value=" 2">2</Option>
                  <Option value=" 3">3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          {/* <Col span={8}>
            <Form.Item label="HDSD">{getFieldDecorator('hdsd', {})(<Input />)}</Form.Item>
          </Col> */}
          <Col span={8}>
            <Form.Item label="Ký hiệu phách">
              {getFieldDecorator('system1', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số phách từ">{getFieldDecorator('system2', {})(<Input />)}</Form.Item>
          </Col>
          {/* <Col span={8}>
            <Form.Item label="Cắt số Sinh SV/Phòng">
              {getFieldDecorator('system3', {})(<Input />)}
            </Form.Item>
          </Col> */}
          {/* <Col span={8}>
            <Form.Item label="Cắt số Sinh SV/DS">
              {getFieldDecorator('system30', {})(<Input />)}
            </Form.Item>
          </Col> */}
          {/* <Col span={8}>
            <Form.Item label="Số sinh viên tối đa/túi">
              {getFieldDecorator('system4', {})(<Input />)}
            </Form.Item>
          </Col> */}
          {/* <Col span={8}>
            <Form.Item label="Đánh phách trộn DS">
              {getFieldDecorator('system40', {})(<Checkbox />)}
            </Form.Item>
            <Form.Item label="Cắt từ đầu DS">
              {getFieldDecorator('system41', {})(<Checkbox />)}
            </Form.Item>
          </Col> */}
        </Row>
        {/* <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              
            </div>
          </Col>
        </Row> */}
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <BtnAddNew title="Thêm sinh viên" />
              <BtnDelete title="Xoá sinh viên" />
            </div>
            <div className="btn-group" style={{ float: 'right' }}>
              <Button type="primary">Lập số phách </Button>
              <BtnDelete title="Xoá" />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar1;



